
.paginator{
    margin:10px;
}

.pageNumber {
    padding:2px;
    border: 1px solid grey;
}

.selectedPage {
    font-weight: bold;
    color:#e44d3a;
    cursor: pointer;
}

.page{
    cursor: pointer;
}


.button{
    width: 50px;
    height: 30px;
    color: #f2f2f2;
    background-color: #53d690;
    border: none;
    border-radius: 6px;
}

.button:hover {
    background-color: #51a5fb;
    transition: 0.3s;
}